  .footer {
    display: flex;
    flex-flow: row wrap;
    padding: 30px 50px 20px 50px;
    color: #c2d6d7;
    background-color: #20282d;
  }

  .footer > * {
    flex: 1 100%;
  }
  
  .info-span {
    margin-right: 1.25em;
    margin-bottom: 2em;
  }

  .info-span p {
    margin: 0px 54px 10px;
  }

  p, li {
    font-size: 18px;
  }
  
  .nav-title {
    font-weight: 600;
    font-size: 18px;
    color: #fff;
  }
  
  .footer ul {
    list-style: none;
    padding-left: 0;
  }
  
  .footer li {
    line-height: 2em;
  }
  
  .footer a {
    text-decoration: none;
  }
  
  .footer-nav {
    display: flex;
    flex-flow: row wrap;
  }
  
  .footer-nav > * {
    flex: 1 50%;
    margin-right: 1.25em;
  }
  .nav-ul a {
    color: #c2d6d7;
  }
  
  .nav-ul a:hover {
    color: cyan;
    text-decoration: none;
  }
  
  .nav-ul-extra {
    column-count: 2;
    column-gap: 1.25em;
  }
  
  .footer-dash {
    text-align: center;
    padding-top: 2px;
    width: 90px;
    height: 2px;
    margin-top: -32px;
    margin-bottom: 12px;
    background: linear-gradient(
        45deg,
        darkcyan 0%,
        #00ECFA
        90%
    );
    background-size: 100% 5px;
    /* if linear-gradient, we need to resize it */
  }
  
  .footer-text {
    padding-top: 10px;
    text-align: center;
    font-size: 0.8rem;
    background: #20282d;
  }

  .footer-img {
    margin-top: 17px;
  }
  
  .nav-item {
    margin-bottom: 3.5rem;
    margin-left: 40px;
  }
  
  /* Social Icons styles */
  :root {
    --r: 12px;
    /* corner radius */
    --w: 4px;
    /* width of the border */
  }

  .col .social li a {
    position: relative;
    font-size: 20px;
    text-align: center;
    margin-top: 0px;
    margin-right: 20px;
    margin-left: 0px;
    margin-bottom: 40px;
    transition: 0.5s;
    float: left;
  }

  .col .social li a:hover {
      color: cyan;
  }
  
  .col .social,
  .col .social * {
    box-sizing: content-box;
  }
  
  @media screen and (min-width: 40.375em) {
    .footer-nav > * {
      flex: 1;
    }

    .info-span {
      flex: 1 0px;
    }
  
    .footer-nav {
      flex: 2 0px;
    }
  }

  @media screen and (max-width: 750px) {
    .info-span p {
        margin: 0px 0px 10px;
    }
    .footer-img {
        margin-left: -54px;
    }
    .nav-item {
        margin-bottom: 3.5rem;
        margin-left: 0px;
    }
  }

  @media screen and (max-width: 510px) {
    .col .social {
      display: grid;
      grid-template-columns: 50% 50%;
      width: fit-content;
    }
  }